
import Scroll from '../../../base/scroll/scroll'
import List from '../../../base/list/list'
import Vue from 'vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import { scrollPages } from '../../../assets/js/mixin'
Vue.use(ConfirmPlugin)
export default {
name: 'desPostList',
mixins: [scrollPages],
components: {
Scroll,
List
},
data () {
return {
showToast: false,
list: [],
apiUrl: '/api/withdrawApply/buyer/getBuyerApplyRecord'
}
},
computed: {
params: {
get () {
return {
buyerUserId: this.userInfo.buyerUserId,
pageNo: this.pageNo,
pageSize: this.pageSize
}
},
set (val) {
return val
}
}
},
methods: {

setInfo (data) {
let mylist = []
this.maxPageSize = data.totalCount
for (let item of data.datas) {
let status = 0
let info = '提现失败'
if (parseInt(item.status) === 0) {
status = 2
info = '处理中'
} else if (parseInt(item.status) === 1) {
status = 1
info = '到账成功'
}
let obj = {
title: item.productName,
time: item.applyTime,
money: item.cash,
unit: '元',
status: status,
info: info
}
mylist.push(obj)
}
this.list = [...this.list, ...mylist]
this.$nextTick(() => {
this.$refs.listArr.refresh()
this.canLoading = true
})
}
}
}



